@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;600;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: "Outfit", sans-serif;
  background-color: #100f0f;
}

.main {
  min-height: 90vh;
  max-width: 450px;
  margin: 0 auto;
  padding: 40px 20px;
}

.main h1 {
  color: #f8f7f7;
  text-align: center;
  font-size: 1.3em;
}

footer {
  color: #c6c3c3;
  font-weight: 300;
  text-align: center;
  padding: 20px 0;
}

footer a {
  color: #fff;
  font-weight: 400;
}
